@import "../../../node_modules/slick-carousel/slick/slick.scss";

.top {
  padding-bottom: 50px;
  &__inner {
    padding-bottom: 70px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      background-image: url('../img/content/slider-bg.webp');
      width: 100%;
      height: 722px;
      top: 0;
      z-index: -1;
    }
  }
  &__slider-item{
    display: flex !important;
    justify-content: center;
    align-items: center; 
  }
  &__slider-img {
    margin-left: -14%;
    min-width: 703px;
    height: 815px;
    object-fit: cover;
  }
  &__slider-info {
    max-width: 645px;
    margin-left: 25px;
  }
  &__slider-title {
    color: $title;
    font-size: 110px;
    line-height: 135px;
    padding-bottom: 26px;
    margin-bottom: 26px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 185px;
      background-color: #6d7278;
      height: 1px;

    }
  }
  &__slider-text {
    max-width: 585px;
  }
  // .slick-dots {

  // }
}






