.footer {
  &__content {
    background: #dfdfdf;
    padding: 60px 0 80px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__info {
    width: 540px;
  }

  &__info-title {
    color: $title;
    @extend %title-font;
    margin-bottom: 14px;
    line-height: 23px;
  }

  &__info-text {
    font-size: 16px;
    line-height: 27px;
    color: $default;
    margin-bottom: 16px;
    max-width: 475px;

  }

  &__info-email {
    display: inline-block;
    font-weight: 600;
    font-size: 14px;
    line-height: 28px;
    color: $title;
    margin-bottom: 22px;
    padding-left: 25px;
    position: relative;

    &--icon {
      position: absolute;
      left: 1px;
      top: 7px;
    }
  }

  &__form {
    display: flex;
  }

  &__form-input {
    padding: 6px 10px 6px 18px;
    border-radius: 21px;
    background-color: transparent;
    border: 1px solid rgba($title, .7);
    width: 100%;

    &::placeholder {
      font-family: 'Open Sans', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 27px;
      color: #A1A1A1;
    }
  }

  &__form-button {
    color: #FFFFF0;
    background-color: $title;
    min-width: 160px;
    border-radius: 21px;
    border: none;
    padding: 7px 12px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 27px;
    margin-left: 9px;
    cursor: pointer;
  }

  &__social {
    width: 125px;
    margin-right: auto;
    margin-left: 100px;
  }

  &__social-item,
  &__menu-item {
    font-size: 14px;
    font-weight: 600;
    line-height: 28px;
    margin-bottom: 15px;
  }

  &__social-link,
  &__menu-link {
    color: #707070;
  }

  &__social-link {
    padding-left: 30px;
    position: relative;

    &--icon {
      position: absolute;
      top: 1px;
      left: 1px;
    }
  }

  &__menu {
    margin-right: 60px;
    width: 125px;
  }

  &__copyright {
    background-color: $title;
    color: #a1a1a1;
    text-align: center;
    padding: 42px 0 42px;
  }
}